.actionPanel {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 auto;
  margin-top: auto;
  max-width: 600px;
  width: 100%;
}

.actionButton {
  display: flex;
  align-items: center;
  column-gap: 5px;
  color: #fff;
}

.savingButton {
  background-color: #990000;
  transition: all 0.4s ease;
}

.savingButton.saved {
  background-color: #008a39;
  color: #fff;
}

@media (max-width: 450px) {
  .actionPanel {
    flex-direction: column;
    row-gap: 20px;
  }
}
